.footer {
    background-color: #F7F7F7;
}

.footer-close {
    fill: var(--primary_color);

    &:hover,
    &:focus {
        fill: var(--secondary_color);
    }
}

.footer-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 30px;
}

.footer-text {
    font-size: 14px;
    line-height: 1.17;
    font-weight: 400;
    color: #545454;

    @media screen and (min-width: 768px) {
        font-size: 16px;
        line-height: 1.18;
    }
}

.footer-link {
    display: inline-block;
    color: #545454;
    text-decoration: underline;

    @media screen and (min-width: 768px) {
        font-size: 16px;
        line-height: 1.19;
    }

    &:hover,
    &:focus {
        color: var(--secondary_color);
    }
}

.heart-icon {
    margin: 0px 10px 0px 10px;
    fill: currentColor;
    animation: heartbeat 1s infinite;
}

@keyframes heartbeat {
    0% {
        transform: scale(1.10);
    }

    30% {
        transform: scale(1.4);
    }

    60% {
        transform: scale(1.9);
    }
}


.footer-police {
    @media screen and (max-width: 767px) {
        display: block;
        margin-bottom: 10px;
    }
}






body.lock {
    overflow: hidden;
}


.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    
    
    overflow-y: auto;
    overflow-x: hidden;
   
    
    transition: all 0.8s ease 0s;
    z-index: 10000;
}


.popup.open {
    opacity: 1;
    visibility: visible;
}


.popup.open .popup__content {    
    transform: perspective(600px) translate(0px, 0%) rotateX(0deg);
    opacity: 1;
}


.popup__area {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.popup__body {
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px 10px;
}

.popup__content {
    background-color: #cebfb9;
    color: var(--primary_color);
    max-width: 1000px;
    padding: 20px;
    position: relative;
                
    transition: all 0.8s ease 0s;
    opacity: 0;
    
    transform: perspective(600px) translate(0px, 200%) rotateX(45deg);

}

.popup-border {
    border-radius: 15px;
}

.popup__close {
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: 20px;
    color: var(--primary_color);
    text-decoration: none;
    padding: 10px;
}


.popup__title {
    font-size: 20px;
    margin: 0px 0px 1em 0px;
}

.neons {
    margin-top: 20px;
    text-align: center;
}

.neons h1 {
    font-size: 40px;
    text-align: center;
    font-weight: bold;
    -webkit-animation: glow 2s ease-in-out infinite alternate;
    -moz-animation: glow 2s ease-in-out infinite alternate;
    animation: glow 2s ease-in-out infinite alternate;
}

@-webkit-keyframes glow {
    from {
        color: #e6630df6;
        text-shadow: 0 0 10px #3e4645, 0 0 20px #3e4645, 0 0 30px #3e4645, 0 0 40px #3e4645, 0 0 50px #3e4645, 0 0 60px #3e4645, 0 0 70px #3e4645, 0 0 90px #3e4645;
    }

    to {
        color: gray;
        text-shadow: 0 0 20px #3e4645, 0 0 30px #3e4645, 0 0 40px #3e4645, 0 0 50px #3e4645, 0 0 60px #3e4645, 0 0 70px #3e4645, 0 0 80px #3e4645, 0 1 90px #3e4645;
    }
}


@media screen and (min-width: 768px) {
    .team {
        &__list {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;           
        }

    }
}



.team-title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 25px;
    font-weight: 700;
    color: var(--primary_color);


        @media screen and (min-width: 480px) {
            font-size: 22px;
        }
    
        @media screen and (min-width: 768px) {
            font-size: 30px;
            flex-direction: inherit;
        }
    
        @media screen and (min-width: 768px) {
            font-size: 40px;
        }
    }



.team-group {
    @media screen and (min-width: 768px) {
        margin-right: 15px;
    }
}

.team-name {
    color: var(--secondary_color);
}

.team__people {
    position: relative;
}

.card-title {
    font-size: 16px;
    margin-bottom: 5px;
    text-align: center;
    color: var(--primary_color);
}

.footer-title-name {
    text-align: center;
    margin: 0;
}

.card-text.footer-text-p {
    margin: 5px 0;
}

.card-text {
    margin: 10px 0;
    color: var(--primary_color);
}

.card {
    position: relative;
    top: 20%;
    left: 50%;
    transform: translate(-50%, 0%);
    perspective: 1000px;
    width: 180px;
    height: 230px;
    margin-right: 7px;
    margin-bottom: 7px;

}

.card-container {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: all 0.7s ease;
    position: absolute;
    box-shadow: 0px 0px 6px 2px #ffffff50;
    border-radius: 20px;
    border: 3px solid var(--secondary_color);
}

.card-front,
.card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    

    background: #f7f7f7;

    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 17px;
}

.card-photo {
    width: 130px;
    height: 130px;
    background-size: cover;
    background-position: center;
    border-radius: 50%;
    margin-bottom: 10px;
    margin-top: 10px;

}


